<template>
  <div class="page-map">

    <mt-header title="实惠住宿">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <nav class="tab-city">
      <mt-button @click.native="showPopup('province')" 
                size="large" 
                :class="{'active':popupStatus == 'province' && popupVisible}"
                ref="button">
        {{province}}
      </mt-button>
      <span class="tab-line"></span>
      <mt-button @click.native="showPopup('city')" 
                size="large"
                :class="{'active':popupStatus == 'city' && popupVisible}"
                ref="button">
        {{city}}
      </mt-button>

      <mt-popup v-model="popupVisible" popup-transition="popup-fade" class="city-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li v-if="popupStatus == 'province'" 
              @click="selectProvince(item)"
              :class="{'active':item == province}"
              v-for="(item, index) in provinceList">
            {{item}}
          </li>
          <li v-if="popupStatus == 'city'"  
              @click="selectCity(item)"
              :class="{'active':item == city}"
              v-for="(item, index) in cityList">
            {{item}}
          </li>
        </ul>
      </mt-popup>
    </nav>

    <div id="allmap" v-bind:style="mapStyle"></div>

    
    
  </div>
</template>


<script>
import address from '../common/address.js'
import '../css/city.css';

export default{
  name: 'page-map',

  data : function(){
    return{
      provinceList:[],
      cityList:[],
      address: ['北京', '北京'],
      province: '北京',
      city: '北京',
      popupVisible: false,
      popupStatus: 'province',
      buttonBottom: 0,
      spread: false,
      pointVisible: false,
      mapStyle:{
        width:'100%',
        height:this.mapHeight +'px'
      }
    }
  },
  props: {
    // 地图在该视图上的高度
    mapHeight:{
      type:Number,
      default:500
    },
    // 经度
    longitude:{
      type:Number,
      default:116.404
    },
    // 纬度
    latitude:{
      type:Number,
      default:39.915
    },
    description:{
      type:String,
      default:'天安门'
    }
  },
  // ready:function(){
  //   var map =newBMap.Map("allmap");
  //   var point =newBMap.Point(this.longitude,this.latitude);
  //   var marker =newBMap.Marker(point);
  //   map.addOverlay(marker);
  //   map.centerAndZoom(point,15);
  //   // 信息窗的配置信息
  //   var opts ={
  //     width :250,
  //     height:75,
  //     title :"地址："
  //   }
  //   var infoWindow =newBMap.InfoWindow(this.description, opts);// 创建信息窗口对象
  //   marker.addEventListener("click",function(){
  //     map.openInfoWindow(infoWindow,point);
  //   });
  //   map.enableScrollWheelZoom(true);
  //   map.openInfoWindow(infoWindow,point);//开启信息窗口
  // }

  methods: {
    showPopup (type) {
      this.popupStatus = type;  //切换省、城市
      
      this.popupVisible = true;
    },
    
    selectProvince (item) {
      this.province = item;
      this.cityList = address[item];
      this.city = address[item][0];   //默认选中第一个

      this.address = [this.province, this.city];

      this.popupVisible = false;
    },

    selectCity (item) {
      this.city = item;
      this.popupVisible = false;
    },

    handleMore () {
      this.spread = true;
    },

    ready: function() {
      var map = new BMap.Map('allmap');
      var point = new BMap.Point(104.075796, 30.659684);
      map.centerAndZoom(new BMap.Point(104.047404, 30.696035), 14);
      map.addControl(new BMap.MapTypeControl());
      map.setCurrentCity('小龙坎火锅');
      map.enableScrollWheelZoom(true);
      map.enableDoubleClickZoom(true);
      var marker = new BMap.Marker(point);
      map.addOverlay(marker);
    }
  },

  created() {
    this.provinceList = Object.keys(address);
    this.cityList = address[this.province];
  },

  mounted() {
    this.ready();

    this.buttonBottom = this.$refs.button.$el.getBoundingClientRect().bottom+1;
  }

}
</script>

<style lang="css">

  .fix-map .mint-button--normal{
    width:100%;
    background:url(../../images/down_icon@3x.png) #fff 80% 20px no-repeat;
    background-size: 12px auto;
  }

  .fix-map .mint-button--normal span{
    color: #0977C5;
  }
  .fix-map .mint-button-text{
    margin-left:-20px;
  }

  .list-hotel-popup ul{

  }
  .hotel-item{
    padding:10px 15px;
    background:#fff;
    border-bottom: 1px solid #E9E9E9;
  }
  .hotel-item h3{
    display: flex;
    align-items:center;
    margin: 5px 0 10px;
    font-size:17px;
    color: #1E1E1E;
  }
  .hotel-item p{
    font-size:15px;
  }
  .hotel-item span{
    margin-left:8px;
    padding:0 5px;
    height:16px;
    font-size:12px;
    font-weight:400;
    color:#0977C5;
    background: #fff;
    border: 1px solid #0977C5;
    border-radius: 2px;
  }

  .hotel-info p{
    margin:5px 0;
    font-size: 15px;
    color: #828282;
  }

  .hotel-popup{
    width: 100%;
  }

  .hotel-popup .mint-button--normal{
    background:url(../../images/up_icon_grey@3x.png) #fff 80% 20px no-repeat;
    background-size: 12px auto;
  }
 
</style>




