<template>
  <div class="page-booking-date">

    <mt-header title="入住离店时间">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>


    <div class="box">
      <calendar :range="calendar.range"  :value="calendar.value" :zero="calendar.zero" :lunar="calendar.lunar" :begin="calendar.begin" :end="calendar.end" @select="calendar.select"></calendar>
    </div>

  </div>
</template>

<script>
import calendar from '../../calendar/calendar.vue'

export default {
  name: 'page-booking-date',

  components: {
    calendar
  },

  data(){
      return {
          count: 0,

          calendar: {
              show:false,
              range:true,
              zero:true,
              value:[], //默认日期
              select:(begin,end)=>{
                this.calendar.value=[begin,end];
                let total = this.dateDiff(begin.join("-"),end.join("-"));
                this.count = total;

                let data = {
                    begin: begin[1]+"月"+begin[2]+"日",
                    end: end[1]+"月"+end[2]+"日",
                    count: total
                };
                let dataString = JSON.stringify(data);
                localStorage.setItem("booking-date", dataString);
              }
          }
      }
  },

  mounted() {
      if(this.calendar.value.length<2){
        setTimeout(() => {
          this.$toast('请选择入住和离店时间')
        }, 500)
        
      }
  },

  methods:{
    back () {
      if(this.calendar.value.length == 2 && this.count < 1){
        this.$toast('入住时间至少一天以上')
      }else{
        window.history.back();
      }
      
    },

    //计算天数差的函数，日期格式: yyyy-mm-dd
    dateDiff (sDate1,  sDate2) {
       let  aDate,  oDate1,  oDate2,  iDays  
       aDate  =  sDate1.split("-")  
       oDate1  =  new  Date(aDate[1]  +  '-'  +  aDate[2]  +  '-'  +  aDate[0])    //转换为mm-dd-yyyy格式  
       aDate  =  sDate2.split("-")  
       oDate2  =  new  Date(aDate[1]  +  '-'  +  aDate[2]  +  '-'  +  aDate[0])  
       iDays  =  parseInt(Math.abs(oDate1  -  oDate2)  /  1000  /  60  /  60  /24)    //把相差的毫秒数转换为天数  
       return  iDays;
    }
  }
};
</script>

<style lang="css">

</style>


